<template>
  <div class="pipelineadd">
    <v-dialog v-model="dialog">
      <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="enterAdd">mdi-plus-circle</v-icon>
      </template>

      <v-card>
        <v-card-title :class="titleClass" primary-title>{{title}}</v-card-title>

        <v-card-text>
          <v-form ref="form">
            <v-row class="align-center">
              <v-col cols="4">
                <v-select
                  :items="pipelineItems"
                  label="使用管路選擇"
                  v-model="list.name"
                  :rules="emptyRules"
                ></v-select>
              </v-col>
              <v-col cols="4">
                <v-text-field
                  label="起始日期"
                  type="date"
                  v-model="list.start_date"
                  :rules="emptyRules"
                ></v-text-field>
              </v-col>
              <v-col cols="4">
                <v-text-field label="結束日期" type="date" v-model="list.end_date"></v-text-field>
              </v-col>
              <v-col cols="4">
                <v-text-field label="單位(Fr)" v-model="list.unit"></v-text-field>
              </v-col>
              <v-col cols="8">
                <v-text-field label="備註" v-model="list.note"></v-text-field>
              </v-col>
            </v-row>
          </v-form>

        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="primary" @click="addOK" v-if="process=='add'" :loading="loading" :disabled="loading">確認新增</v-btn>
          <v-btn class="success" @click="editOK" v-if="process=='edit'" :loading="loading" :disabled="loading">確認修改</v-btn>
        </v-card-actions>
        <!-- <pre>{{list}}</pre> -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import qs from "qs";
import dayjs from "dayjs";

export default {
  data() {
    return {
      dialog: false,
      list: {}, //目前操作的表單內容
      oldList: {}, //存放修改前資料
      emptyRules: [v => !!v || "不可空白"],
      title: "", //抬頭
      titleClass: "", //抬頭顯示樣式
      process: "", //儲存執行動作
      pipelineItems: [
        "一般鼻胃管",
        "矽質鼻胃管",
        "一般尿管",
        "矽質尿管",
        "三叉導尿管",
        "氣切",
        "胃造口",
        "腸造口",
        "Pig-tail",
        "PTGBD",
        "膀胱造瘻管",
        "雙腔導管",
        "胸管",
        "PCN",
      ],
      loading:false,
    };
  },

  mounted() {
    //父組件要執行本組件時的function時的設定 (showFromParent) id是暫存傳過來的值
    this.$on("childFn", function(item) {
      this.dialog = true;
      this.enterEdit(item);
    });
  },

  methods: {
    enterAdd() {
      //執行新增流程
      this.process = "add";
      this.title = "新增使用管路";
      this.titleClass = "font-weight-black error lighten-2";
      this.list = {
        start_date: dayjs().format("YYYY-MM-DD")
      };
    },
    enterEdit(item) {
      //執行修改流程
      this.process = "edit";
      this.title = "修改使用管路";
      this.titleClass = "font-weight-black success lighten-2";
      this.oldList = item; //暫存未修改前資料
      this.list = item; //列表中複製一份顯示及修改用資料
    },
    addOK() {
      //確認新增
      if (this.$refs.form.validate()) {
        var postData = {
          u_snkey: this.$store.state.uData.snkey,
          datalist: JSON.stringify(this.list),
          create_man:
            this.$store.state.pData.username +
            "(" +
            dayjs().format("YYYY-MM-DD HH:mm:ss") +
            ")" //執行動作的人,時間,內容
        };

        this.loading = true;
        this.axios
          .post(
            "general/add/" + this.$store.state.databaseName + "/pipeline_used",
            qs.stringify(postData)
          )
          .then(rs => {
            console.log(rs.data);
            if (rs.data.state == 1) {
              var pop = { msg: "已新增", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.list = {};
              this.dialog = false;
              this.$emit("getAllData");
              this.loading = false;
            }
          });
      } else {
        var pop = {
          msg: "資料輸入不完全!!請重新確認!!",
          type: true,
          theme: "warning"
        };
        this.$store.commit("snackbar", pop);
      }
    },
    editOK() {
      //確認修改
      if (this.$refs.form.validate()) {
        var postData = {
          snkey: this.list.snkey,
          u_snkey: this.$store.state.uData.snkey,
          datalist: JSON.stringify(this.list),
          edit_man:
            this.$store.state.pData.username +
            "(" +
            dayjs().format("YYYY-MM-DD HH:mm:ss") +
            ")" //執行動作的人,時間,內容
        };

        this.loading = true;
        this.axios
          .post(
            "general/edit/" + this.$store.state.databaseName + "/pipeline_used",
            qs.stringify(postData)
          )
          .then(rs => {
            if (rs.data.state == 1) {
              var pop = { msg: "已修改", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.dialog = false;

              for (var i in this.list) {
                if (this.oldList[i] == undefined) {
                  this.$set(this.oldList, i, "");
                }
                //將各個欄位的資料透過一對一的去比對,就可以使vue對資料會有反應
                this.oldList[i] = this.list[i];
              }
              this.loading = false;
            }
          });
      } else {
        var pop = {
          msg: "資料輸入不完全!!請重新確認!!",
          type: true,
          theme: "warning"
        };
        this.$store.commit("snackbar", pop);
      }
    },
    getFee() {
      //取得所有病歷資料
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      //取得所有資料
      this.axios
        .post("general/getAll/" + this.$store.state.databaseName + "/fee")
        .then(rs => {
          this.feeItems = rs.data.map(i => {
            return {
              snkey: i.snkey,
              text: i.name,
              price: i.price
            };
          });
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    }
  }
};
</script>

<style lang="scss" scoped>
thead {
  th {
    font-size: 1.2rem;
    font-family: "微軟正黑體";
    color: red;
  }
}
tbody {
  td {
    font-size: 1rem;
  }
}
</style>